
.file-list-box {
  position: relative;
  padding: 1rem;
}
.file-list-box > header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background: #fff;
}
.file-list-box > header h1 {
      color: #333;
      font-size: 2rem;
      line-height: 3.5rem;
}
.file-list-box > header h1 span {
        cursor: pointer;
}
.file-list-box main ul.file-list {
    margin: .5rem 0 1rem;
}
.file-list-box main ul.file-list li {
      line-height: 1.6rem;
}
.file-list-box main ul.file-list li svg {
        width: 1.1rem;
        vertical-align: text-top;
}
.file-list-box main ul.file-list li svg path {
          fill: #333;
}
.file-list-box main ul.file-list li a {
        color: #333;
        font-size: 1.2rem;
        cursor: pointer;
}
.file-list-box main ul.file-list li a:hover {
          text-decoration: underline;
}
@media (max-width: 800px) {
.main-app main ul.file-list li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 3.2rem;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    justify-self: center;
}
.main-app main ul.file-list li svg {
      width: 1.5rem;
      margin-right: .5em;
}
.main-app main ul.file-list li a {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      font-size: 1.3rem;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -ms-flex-item-align: stretch;
          align-self: stretch;
      vertical-align: middle;
}
}

/* markdown css
 * 2018-4-26
 */
.markdown-mod {
  padding: 1rem;
  font-size: 14px;
  color: #333;
  position: relative;
}
.markdown-mod h1 {
    font-size: 2rem;
}
.markdown-mod h2 {
    font-size: 1.5em;
}
.markdown-mod h3 {
    font-size: 1.3em;
}
.markdown-mod h1, .markdown-mod h2, .markdown-mod h3 {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    padding: 5px 0;
    margin: 0 0 5px;
    background: #fff;
}
.markdown-mod h1:after,
  .markdown-mod h2:after,
  .markdown-mod h3:after,
  .markdown-mod h4:after {
    content: '';
    display: block;
    border-bottom: 1px solid #eee;
}
.markdown-mod ul {
    padding: 0 0 0 2em;
    list-style: inherit;
}
.markdown-mod ul li {
      margin: 3px 0;
}
.markdown-mod p {
    margin: 3px 0;
}
.markdown-mod a {
    color: #09f;
    font-weight: 400;
}
.markdown-mod a:hover {
      text-decoration: underline;
}
.markdown-mod table {
    margin: 1rem 0;
}
.markdown-mod table td, .markdown-mod table th {
      padding: .4rem .5rem;
      border: 1px solid #ddd;
}
code, kbd, pre, samp, tt {
  font-family: "Roboto Mono", Monaco, courier, monospace;
  font-size: 0.8em;
  background-color: #f8f8f8;
  -webkit-font-smoothing: initial;
  -moz-osx-font-smoothing: initial;
}
code {
  color: #42b983;
  padding: 3px 5px;
  margin: 0 2px;
  border-radius: 2px;
}



